import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, fetchEncryptStream, ObjectKeys, genNumId, parseFloatToDecimal, qryStore, generateComprobanteId, Log } from "../../../stores/Utils";

import { TextInput_Cmpt,  SvgIcon_Cmpt, AccountLabelDetail_Cmpt } from "../../../components";


import "./styles.css"
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import {  getall_collects, getall_toPay, searchSubAccbyCode } from "../services/server_fetching";
import { PickCustomer_Cmpt } from "../../ComprobanteParametrizados";
import { useNavigate } from "react-router";
import { DateCalComponent_Cmpt } from "../../Inventory";
import ExtraExpenses from "./ExtraExpenses";
import { get_balances_by_type } from "../../CuentasContables/services/server_fetching";

const Svg = SvgIcon_Cmpt();

const TextInput = TextInput_Cmpt();



const DropInput = PickCustomer_Cmpt();

const DateCalComponent =DateCalComponent_Cmpt();


const AccountLabelDetail = AccountLabelDetail_Cmpt();


const frmNm = "new_multi_collect_prov";
const Acc2Collect = "Acc2MPay";
const multi_collect_by_doc = "multi_collect_by_prov_Doc";
const toCollect_by_doc = "collect_by_docM";
const multi_collect_2S = "multi_collect_2S";

const _extraExp = "extraAccountListOnCollect" 

const CobroManual  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const navigate = useNavigate();

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			get_balances_by_type("global_accounts","global_account_params",cb_result);
			updStore("PamentMethodAccount", null);
			updStore(multi_collect_2S, null);
			updStore(toCollect_by_doc, null);
			updStore(multi_collect_by_doc, null);
			updStore("accountPicker", {});
			set_obs(gen16CodeId())
		}
	});

	const cb_result = () => {
		set_obs(gen16CodeId())
	};
	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm) || {};

		let comp2save = [];

		if(!ffm["date"]){
			ffm["date"] = (new Date()).getTime()+800;
		}
		else{
			ffm["date"] =(new Date(ffm["date"])).getTime();
		}


		let comprobanteId = await generateComprobanteId(ffm["date"]);
		ffm["comprobanteId"] =comprobanteId;


		let reference = ""




		let arr_anticipo = getStore("multi_collect_anticipo_by_doc") && ObjectKeys(getStore("multi_collect_anticipo_by_doc"));
		arr_anticipo && arr_anticipo?.map((flds, indE)=>{

			let nnfrm = {...ffm};
			let itm = getStore("multi_collect_anticipo_by_doc")[flds];

			nnfrm["document"] =  itm?.document;
			//reference += itm?.document +" ; ";
			nnfrm["account"] = itm?.account;
			nnfrm["subAccount"] = itm?.subAccount;
			//nnfrm["providerId"] = itm?.providerId;
			//nnfrm["customerId"] = itm?.customerId;
			nnfrm["type"] = itm?.type;
			nnfrm["amount"] = itm?.amount;
			//nnfrm["collectOrCollect"] = "collect";
			nnfrm["operationId"] = gen16CodeId();

			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm;
			comp2save.push(bdyq2);


			/*******************************/

			let nnfrmCC = {...ffm};
			nnfrmCC["document"] =  itm?.documentRefer;
			nnfrmCC["account"] = itm?.accountRef;
			nnfrmCC["subAccount"] = itm?.sub_accountRef;
			nnfrmCC["type"] = "credit";
			nnfrmCC["amount"] = itm?.amount;
			nnfrmCC["operationId"] = gen16CodeId();

			let bdyqCC = {...getStore(qryStore())?.add_opera};
			bdyqCC.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyqCC.form=nnfrmCC;
			comp2save.push(bdyqCC)
			
		});

		


		let arr = getStore(multi_collect_2S) && ObjectKeys(getStore(multi_collect_2S));
		arr && arr?.map((flds, indE)=>{

			let nnfrm = {...ffm};
			let itm = getStore(multi_collect_2S)[flds];

			nnfrm["document"] =  itm?.document;
			reference += itm?.document +" ; ";
			nnfrm["account"] = itm?.account;
			nnfrm["subAccount"] = itm?.subAccount;
			nnfrm["providerId"] = itm?.providerId;
			nnfrm["customerId"] = itm?.customerId;
			nnfrm["type"] = itm?.type;
			nnfrm["amount"] = itm?.amount;
			nnfrm["collectOrCollect"] = "collect";
			nnfrm["operationId"] = gen16CodeId();

			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm;

			
			comp2save.push(bdyq2)
			
		});

		

		
		let arrExtra = getStore(_extraExp) && ObjectKeys(getStore(_extraExp));

		let bankGlobal = getStore("global_account_params")?.cash_bank;

		arrExtra && arrExtra?.map((flds, indE)=>{

			let nnfrm = {...ffm};
			let itm = getStore(_extraExp)[flds];


		

			nnfrm["document"] = nnfrm["paymentDocument"];
			nnfrm["account"] = itm?.account;
			nnfrm["subAccount"] = itm?.subAccount;
			nnfrm["type"] = "debit";
			nnfrm["amount"] = itm?.amount;
			nnfrm["operationId"] = gen16CodeId();

			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm;
			comp2save.push(bdyq2)
			

			let nnfrm4 = {...ffm};

			nnfrm4["document"] = nnfrm4["paymentDocument"];
			nnfrm4["paymentDocument"]= reference;
			nnfrm4["account"] = bankGlobal?.account;
			nnfrm4["type"] = "credit";
			nnfrm4["amount"] =  itm?.amount*1;
			
			nnfrm4["operationId"] = gen16CodeId();

			let bdyq4 = {...getStore(qryStore())?.add_opera};
			bdyq4.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq4.form=nnfrm4;

			comp2save.push(bdyq4);
			
		});
		


		let amount = SumCollects(getStore(multi_collect_2S));
		if(amount>0){

			
			let nnfrm3 = {...ffm};
			nnfrm3["document"] = nnfrm3["paymentDocument"];
			nnfrm3["paymentDocument"]= reference;
			nnfrm3["account"] = bankGlobal?.account;
			let accountDeb = getStore("account_ids")?.[nnfrm3["account"]];
			if(accountDeb.collectOrCollect){
				nnfrm3["collectOrCollect"] = accountDeb.collectOrCollect;
			}
			nnfrm3["type"] = "debit";
			nnfrm3["amount"] = amount*1;
			
			nnfrm3["operationId"] = gen16CodeId();

			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm3;


			comp2save.push(bdyq2)
		}
		//console.log(comp2save)
	
		const promises = comp2save.map(async (qry2Sv) => {
			const res = fetchEncryptStream(qry2Sv);
			const td = await res;
			if (td) {
				//console.log(td)
			}
        })      
        await Promise.all(promises);

		setTimeout(() => {
			updStore(multi_collect_by_doc, null);
			updStore(frmNm, null);
			updStore("multi_collect_anticipo_by_doc", null);
			data?.modalID && CloseModal({id:data?.modalID});
			navigate({
				pathname: "/accounting-ledger" ,
				search: `?cId=${comprobanteId}`
			});
		}, 400);

		
		/*
		
		 */
	
	};





	



	

	
	const add2F = () => {
		let frm2 = {...getStore(multi_collect_by_doc)} || {};
		let multi_collect_2S_frm = {...getStore(multi_collect_2S)} || {};
		ObjectKeys(frm2).map(fl=>{
			let kId =  getStore(Acc2Collect)?.account + "_"+getStore(Acc2Collect)?.subAccount+"_"+fl;
			let accDtls = {...getStore(Acc2Collect)};
			multi_collect_2S_frm[kId] = {};
			multi_collect_2S_frm[kId].amount = frm2[fl] * 1;
			multi_collect_2S_frm[kId].document = fl;
			multi_collect_2S_frm[kId].type = "credit";
			multi_collect_2S_frm[kId].account = accDtls?.account;
			multi_collect_2S_frm[kId].subAccount = accDtls?.subAccount;
			multi_collect_2S_frm[kId].customerId = accDtls?.customerId;
		})
		updStore(multi_collect_2S,multi_collect_2S_frm);
		updStore(multi_collect_by_doc,null);
		updStore(Acc2Collect,null);
		set_obs(gen16CodeId())
	};


	const openAddView =  () => {
		
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <CustomerListingAccounts updObs={set_obs} confirm={add2F} />
		OpenModal(dataModal);
	}

	let bankGlobalAcc = getStore("global_account_params")?.cash_bank;



	return (
		
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "720px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Registrando Cobro
				</p>
				<div className="flexSpace"/>
				
			</div>
				
			

			<div className="_dsplFlx"  >
				<div className="flexSpace"/>
				{ bankGlobalAcc?.account?
					<div className=" acc_dtls">			
						<div className="_dsplFlx"  >
							<div className="code" style={{color: "#646cff"}} >
								{getStore("account_ids")?.[bankGlobalAcc?.account]?.code} 
							</div>
							<div className="name">
							{getStore("account_ids")?.[bankGlobalAcc?.account]?.name}
							</div>
						</div>
						<div className="flexSpace"/>
					</div>
					:null
				}
			</div>
				

			<div className="_dsplFlx ">
				<DateCalComponent init={getStore(frmNm)?.["date"]} updDate={(e)=>updFrm("date",e)} />
               
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["paymentDocument"]} 
					label={"Documento de Cobro"} 
					width={200} 
					updChanges={(e)=>updFrm("paymentDocument",e)}
				/>
			</div>
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>


			<div className=" _dsplFlx" >
				
				<div className="flexSpace"/>
				<p className="group_title addBtn purple" onClick={openAddView}>
					Clientes
				</p>
			</div>


		{getStore(multi_collect_2S) && ObjectKeys(getStore(multi_collect_2S)).length>0?
		<div className="boxAddAcc" style={{margin: "18px 0"}}>
			<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
				<div className="tr_bar tr_bar_25">
					Cuenta
				</div>
				<div className="tr_bar tr_bar_25">
					Sub Cuenta
				</div>
				<div className="tr_bar tr_bar_25">
					Documento
				</div>
				<div className="tr_bar tr_bar_25">
					Balance
				</div>
			</div>
			<ListingDocs 
					data={{
						account: bankGlobalAcc?.account,
						amount: SumCollects(getStore(multi_collect_2S)),
						"type":"debit",
						document: getStore(frmNm)?.["paymentDocument"]
					}}
					updObs={set_obs}
			/>
			{getStore(multi_collect_2S) && ObjectKeys(getStore(multi_collect_2S))?.map((element, indE)=>

				<ListingDocs 
					key={element} 
					idKey={element} 
					data={getStore(multi_collect_2S)[element]}
					updObs={set_obs}
				/>
			)}
			
		</div>
		:null}


			<ExtraExpenses collectionName={_extraExp} />
			{getStore(frmNm)?.paymentDocument &&( SumCollects(getStore(multi_collect_2S))>0 || SumCollects(getStore("multi_collect_anticipo_by_doc"))>0)?
				<div className="_dsplFlx ">
					<div className="flexSpace"/>
				
					<div className="card">
						<button onClick={save}>
							Guardar
						</button>
					</div>
				</div>
			:null}

			
		</div>
	);
};

export default CobroManual;







const ListingDocs  = ({ data }) => {
	
	
	if(	data?.amount*1=== 0){
		return  null;
	}


	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar tr_bar_25" style={{marginTop: 10}}>
				<AccountLabelDetail 
					data={getStore("account_ids")?.[data?.account]} 
					color={ data.type === "debit"? "#009688":"#c62828"}
					labelField={"code"}
					infoField={"name"}
				/>
			</div>
			<div className="tr_bar tr_bar_25" style={{marginTop: 10}}>
				{data?.subAccount?
				<AccountLabelDetail 
					data={getStore("sub_account_ids")?.[data?.subAccount]} 
					color={ data.type === "debit"? "#009688":"#c62828"}
					labelField={"subAccount"}
					infoField={"subAccountName"}
				/>
				: null}
			</div>
			<div className="tr_bar tr_bar_25" style={{marginTop: 10}}>
				{data?.document }
			</div>
			<div className="tr_bar  tr_bar_25" style={{marginTop: 10}}>
				${(data?.amount*1).toFixed(2) }
			</div>
		</div>
	)

}















const CustomerListingAccounts  = ({ data, confirm }) => {


	const [obs, set_obs] = useState(0);

	const handleSearchAccount = (v) => {
		let nn= {
			"account": v?.account,
			"subAccount": v?.subAccount,
			customerId: v?.customerId
		};
		updStore(Acc2Collect, nn);
		getall_collects(nn,loadSubAcc);
	}


	const loadSubAcc =  () => {
		let subaccountD = getStore("sub_account_ids")?.[getStore(Acc2Collect)?.subAccount];
		searchSubAccbyCode(getStore("global_account_params")?.advance_collections?.account,subaccountD.subAccount ,loadAnticipo);
	}

	const loadAnticipo =  (sbac) => {
		let nn= {
			"account":getStore("global_account_params")?.advance_collections?.account,
			"subAccount":sbac?.sub_accountId
		};

		updStore("advance_collections",nn)
		getall_toPay(nn,cb_result);
	}
	
	const clearCustomer = () => {
		updStore(Acc2Collect, null);
		//updStore(toCollect_by_doc, null);
		set_obs(gen16CodeId())
	};

	const cb_result = () => {
	
		set_obs(gen16CodeId())
	};

	const add = () => {
		confirm && confirm();
		data?.modalID && CloseModal({id:data?.modalID});
	};


	


	
	return(
		<div style={{
			maxHeight: "92vh",
			minHeight:  "90vh",
			width: "720px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Cobros por Clientes
				</p>
				<div className="flexSpace"/>
				
			</div>
			<div  style={{margin: "18px 0"}}>
			{getStore(Acc2Collect)?.account ?
			<div>
				<div className=" acc_dtls">			
					<div className="_dsplFlx"  >
						<div className="code" style={{color: "#646cff"}} onClick={clearCustomer}>
							{getStore("account_ids")?.[getStore(Acc2Collect)?.account]?.code} 
						</div>
						<div className="name">
						{getStore("account_ids")?.[getStore(Acc2Collect)?.account]?.name}
						</div>
					</div>
					<div className="flexSpace"/>
					{getStore(Acc2Collect)?.subAccount ?
					<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
						<div className="code" >
							{getStore("sub_account_ids")?.[getStore(Acc2Collect)?.subAccount]?.subAccount} 
						</div>
						<div className="name">
						{getStore("sub_account_ids")?.[getStore(Acc2Collect)?.subAccount]?.subAccountName} 
						</div>
					</div>
					:null}
				</div>
					<div className="boxAddAcc" style={{margin: "18px 0"}}>

						<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
							<div className="tr_bar tr_bar_25">
								Documento
							</div>
							<div className="tr_bar tr_bar_20">
								Balance
							</div>
							<div className="tr_bar tr_bar_5">
								
							</div>
							<div className="tr_bar tr_bar_25">
								Por Pagar
							</div>
							<div className="tr_bar tr_bar_20">
								Anticipo
							</div>
						</div>
						{getStore(toCollect_by_doc) && ObjectKeys(getStore(toCollect_by_doc))?.map((element, indE)=>
						
							<ListingAccounts 
								key={element} 
								idKey={element} 
								data={getStore(toCollect_by_doc)[element]}
								updObs={set_obs}
							/>
						)}
						
					</div>
				</div>
				:
				
				<DropInput
					placeholder={"buscar un provedor"} 
					width={420}
					handleClick={handleSearchAccount}
				/>
			}
		</div>
		{IsUnder(getStore(toCollect_by_doc))?
		<div className="_dsplFlx ">
				<div className="flexSpace"/>
			
				<p className="group_title addBtn purple" onClick={add}>
						Guardar
				</p>
			</div>
		:null}
	</div>
	)

}






const EstaCuadrado = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let ammm =  parseFloatToDecimal(data?.amount,2);
		if(data?.type === "debit"){ 
			balance +=ammm
		}
		else if(data?.type === "credit"){ 
			balance -= ammm
		}
	});
	return arr && arr?.length>0 && balance === 0;
}  




const SumCollects = (obj) => {
	let balance = 0;
	obj && ObjectKeys(obj)?.map((flds, indE)=>{
		balance += parseFloatToDecimal(obj[flds]?.amount,2)*1;
	});
	return  balance;
}  









const IsUnder = (obj) => {
	let arr = obj && ObjectKeys(obj);
	let frms = getStore(multi_collect_by_doc);
	let isValid = true;
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let kId =  getStore(Acc2Collect)?.account + "_"+getStore(Acc2Collect)?.subAccount+"_"+data["document"];
		let multi_collect_anticipo_by_doc = {...getStore("multi_collect_anticipo_by_doc")}
		let anticipoAmount = multi_collect_anticipo_by_doc?.[kId]?.amount;
		let anticipo = anticipoAmount?anticipoAmount:0;

		let ammm =  parseFloatToDecimal(data?.amount*1 - anticipo*1,2);
		let amount2Pay = parseFloatToDecimal(frms?.[data["document"]]*1,2);
		
		if(amount2Pay && amount2Pay - ammm > 0){
			
			isValid = false;
		}
	});
	return isValid;
}  















const ListingAccounts  = ({ data, updObs, idKey }) => {
	const [amount, set_amount] = useState(0);
	const [obs, set_obs] = useState(0);

	
	let kId =  getStore(Acc2Collect)?.account + "_"+getStore(Acc2Collect)?.subAccount+"_"+data["document"];
	let multi_collect_anticipo_by_doc = {...getStore("multi_collect_anticipo_by_doc")}
	let anticipoAmount = multi_collect_anticipo_by_doc?.[kId]?.amount;


	let anticipo = anticipoAmount?anticipoAmount*1:0;

	const handleChange = (v) => {
		set_amount(v);
		updFrm(data?.document,v)
		updObs(gen16CodeId())
	}

	const cb_result = (v) => {
		
		set_obs(gen16CodeId())
	}
	
	
	const updFrm = (f, v, k) => {
		let frm2 = getStore(multi_collect_by_doc) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(multi_collect_by_doc, frm2);
	};



	

	const openAnticipo =  () => {
		
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <AnticipoModal confirm={cb_result} item={data}/>
		OpenModal(dataModal);
	}

	
	if(	data?.amount*1=== 0){
		return  null;
	}
	return (
		<div className="_dsplFlx hdr_bar">
			<div className=" tr_bar_25" style={{marginTop: 10}}>
				{data?.document }
			</div>
			<div className=" tr_bar_20" style={{marginTop: 10}}>
				${(data?.amount*1 - anticipo).toFixed(2) }
			</div>
			<div className="tr_bar_5" style={{marginTop: 10}}>
				{amount > data?.amount-anticipo ? 
				<div className="icon" >
					<Svg name={"alert"} color={"#c62828"}/>
				</div>
				:null}
			</div>
		 
			<div className=" tr_bar_25">
				<TextInput  
					init={amount} 
					//label={"Descripcion"} 
					type={"number"}
					width={80} 
					updChanges={(e)=>handleChange(e)}
				/>
			</div>
			<div className=" tr_bar_20" style={{marginTop: 10}} onClick={openAnticipo}>
				{anticipo?.toFixed(2)}
			</div>
		</div>
	)

}



const AnticipoModal = ({data, confirm, item}) =>{
	const [activeDoc, set_activeDoc] = useState("");
	
	
	const clear = () => {
		let frm2 = {...getStore("multi_collect_anticipo_by_doc")} || {};

		let kId =  getStore(Acc2Collect)?.account + "_"+getStore(Acc2Collect)?.subAccount+"_"+item["document"];
		delete frm2[kId];
		updStore("multi_collect_anticipo_by_doc",frm2);
		confirm &&  confirm(0);
		data?.modalID && CloseModal({id:data?.modalID});
	}

	const closeModal = (amount) => {
		confirm &&  confirm(amount);
		data?.modalID && CloseModal({id:data?.modalID});
	}
	return (
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "660px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Anticipos por Clientes
				</p>
				<div className="flexSpace"/>
				<p className="group_title addBtn" onClick={()=>clear(false)}>
					Borrar
				</p>
			</div>
	<div className="boxAddAcc" style={{margin: "18px 0"}}>
		<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
			<div className="tr_bar_40 tex_left">
				Cliente
			</div>
			<div className="tr_bar_20">
				Balance
			</div>
			<div className="tr_bar_5"/>
			<div className="tr_bar_25">
				Amount
			</div>
			<div className="tr_bar_10">
				
			</div>
		</div>
		<div className="accounts_container ">
					{getStore("toPay_by_doc") && ObjectKeys(getStore("toPay_by_doc"))?.map((cid, ind5)=>{
						let itm = getStore("toPay_by_doc")[cid];
						let docAmount = itm?.amount*1;
						if( docAmount <= 0){
							return null;
						}
						
						return (
							<AnticipoItem 
								key={itm.document +"_"+ ind5}
								document={item.document}
								itm={itm} 
								activeDoc={activeDoc}
								updDoc={set_activeDoc}
								closeModal={closeModal}
							/>
						)
											
					})}
			</div>
			</div>
	</div>
	)
}





const AnticipoItem = ({itm, closeModal, activeDoc, updDoc, document}) =>{
	const [amount, set_amount] = useState(0);

	let antic = SumAnticipoByDoc(itm["document"],{...getStore("multi_collect_anticipo_by_doc")})
	

	const handleClick = (obj) => {
		let frm2 = {...getStore("multi_collect_anticipo_by_doc")} || {};
		let kId =  getStore(Acc2Collect)?.account + "_"+getStore(Acc2Collect)?.subAccount+"_"+document;
		
		let newO = {...getStore("advance_collections")};
		newO["document"] =  itm["document"];
		newO["documentRefer"] =  document;
		newO["amount"] =  amount*1;
		newO["type"] =  "debit";
		newO["description"] =  obj["description"];

		newO["accountRef"] =  getStore(Acc2Collect)?.account;
		newO["sub_accountRef"] =  getStore(Acc2Collect)?.subAccount;
		frm2[kId] = newO;
		updStore("multi_collect_anticipo_by_doc",frm2);
		closeModal(amount*1);
	}

	const handleUpdDoc = () => {
		updDoc(itm.document);
		set_amount(0);
	}

	return (
		
		<div className="_dsplFlx hdr_bar" style={{margin: "2px 0"}}  >
			<div 
				className="tr_bar_40 tex_left" 
				style={{marginTop: 10, color: activeDoc === itm.document ? "var(--hrm_palette-blue-purple)":""}}
				onClick={handleUpdDoc}
			>
				{itm.document}
			</div>
			<div className="tr_bar_20" style={{marginTop: 10}} >
				${(itm?.amount*1 - antic*1).toFixed(2)} 
			</div>
			<div className="tr_bar_5" style={{marginTop: 10}}>
				{amount > itm?.amount? 
				<div className="icon" >
					<Svg name={"alert"} color={"#c62828"}/>
				</div>
				:null}
			</div>
			
			<div className="tr_bar_25">
			{activeDoc === itm.document ?
				<TextInput  
					init={amount} 
					//label={"Descripcion"} 
					type={"number"}
					width={80} 
					updChanges={(e)=>set_amount(e*1)}
				/>
				:null
			}
			</div>
			<div className="tr_bar_10" style={{marginTop: 10, marginLeft: 9}}>
			{activeDoc === itm.document && amount <= (itm?.amount*1)? 
				<div className="icon" onClick={()=>handleClick(itm)}>
					<Svg name={"add"} color={"var(--hrm_palette-blue-purple)"}/>
				</div>
			:null}
			</div>

		</div>

	)
	

}



const SumAnticipoByDoc = (doc, obj) => {
	let balance = 0;
	obj && ObjectKeys(obj)?.map((flds, indE)=>{
		if(doc===obj[flds]?.document){
			balance += parseFloatToDecimal(obj[flds]?.amount,2)*1;
		}
	});
	return  balance;
}  
